<template>
<div class="component-wrapper">
    <div :class="switchClass">
        <div :class="toggleClass" @click="changeStatusToggle"></div>
    </div>
</div>
</template>

<script>
export default {
    name: 'ToggleSwitch',
    data: function () {
        return {
            toggleClass: null,
            switchClass: null,
        }
    },
    props: {
        isOn: null,
        changeStatusToggle: {
            type: Function,
            default: () => {}
        }
    },
    created() {
        this.changeClassName();
    },
    watch: {
        isOn(val) {
            this.changeClassName();
        }
    },
    methods: {
        changeClassName() {
            this.toggleClass = [
                "toggle-button",
                this.isOn == true ? "toggle-button_position-right" : "toggle-button_position-left"
            ].join(" ");

            this.switchClass = [
                "switch",
                this.isOn == true ? "switch_is-on" : "switch_is-off"
            ].join(" ");
        }
    }
}
</script>

<style lang="css">
@import './Toggle.css';
</style>
