<template>
<div class="mobile-page setting-container">
    <MainLayout :title="$t('Common.Setting.Title')" :leftIcon="`LEFT_ARROW`" :leftAction="() => this.$router.go(-1)">
        <div class="padding-content">
            <div class="push-mess d-flex justify-content-between">
                <div id="content">{{$t('Common.Setting.PushMessage')}}</div>
                <Toggle :isOn="isOn" :changeStatusToggle="changeStatusToggle" />
            </div>
        </div>
    </MainLayout>
</div>
</template>

<script>
import MainLayout from '@/components/Common/Layout/MainLayout'
import Toggle from '@/components/Common/Toggle/Toggle'

export default {
    name: 'Setting',
    components: {
        MainLayout,
        Toggle
    },
    data: function () {
        return {
            isOn: true,
        }
    },
    mounted: function () {},
    methods: {
        async changeStatusToggle() {
            this.isOn = !this.isOn
        }
    }
}
</script>

<style lang="scss" scoped>
.padding-content {
    background-color: #E5E5E5;
}

.push-mess {
    padding: 2rem;
    background-color: #ffffff;

    #content {
        font-family: NotoSansKR;
        font-style: normal;
        font-weight: normal;
        font-size: 1.6rem;
        line-height: 2.4rem;
        color: #333333;
    }
}
</style>
